.about-me-container {
  align-items: center;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px 0;
}

.about-me-container .screen-heading {
  color: black;
}

.about-me-container .screen-sub-heading {
  color: #1f2235;
}

.about-me-container .seperator-line {
  background-color: black;
}

.about-me-parent {
  max-width: 1000px;
  width: 70%;
}

.about-me-card {
  box-shadow: 0 0px 20px -2px #1f2235;
  display: flex;
  margin-bottom: 50px;
  width: 100%;
}

.about-me-profile {
  width: 50%;
  background-image: url("../../assets/AboutMe/me.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.about-me-details {
  width: 48%;
  text-align: justify;
  padding: 30px;
}

.about-me-description {
  font-size: 12px;
}

.about-me-highlights {
  margin: 25px 0 40px;
}

.highlight-heading {
  font-family: "Poppins Bold";
  font-size: 14px;
  margin: 0 0 5px 0;
}

.highlight {
  display: flex;
  align-items: center;
  margin: 5px 0;
  font-size: 12px;
}

.highlight-blob {
  height: 10px;
  width: 10px;
  background-color: #FFC72C;
  border-radius: 50%;
}

.about-me-options .highlighted-btn {
  margin: 0 0 0 30px;
}

.about-me-options {
  display: flex;
  justify-content: space-around;
}

/* IPAD PRO RESPONSIVENESS */
@media only screen and (max-width: 1110px) {
  .about-me-parent {
    width: 88%;
  }
}

@media only screen and (max-width: 898px) {
  .about-me-profile {
    display: none;
  }
  .about-me-details {
    width: 100%;
  }
}

@media only screen and (max-width: 466px) {
  .about-me-options {
    display: flex;
    flex-direction: column;
  }
  .about-me-options .highlighted-btn {
    margin: 24px 0 0 0 !important;
  }
  .about-me-options button {
    width: 100%;
  }
}
